// Variables

$main-color: rgb(41, 171, 226);
$main-color-transparent: rgba(41, 171, 226, 0.5);
$krona-one: krona-one, sans-serif;


// Mixins
@mixin flexify {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex; }
@mixin round-corners {
    border-radius: 1rem; }
@mixin center-block {
    display: block;
    margin-left: auto;
    margin-right: auto; }


html, body {
    width: 100%;
    margin: 0;
    padding: 0;
    overflow-x: hidden; }
header {
    @include flexify;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    position: relative;
    background-image: url(../images/compressed/mountainlake-min.jpg);
    background-position: left;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    img {
        width: 50%; }
    h1 {
        position: absolute;
        bottom: 0rem;
        text-align: center;
        color: white;
        background-color: $main-color-transparent;
        margin: 0;
        padding: 1rem;
        width: 100%;
        font-family: $krona-one;
        font-size: 3.5rem; } }

section {
    margin: 7rem 0 7rem;
    h2 {
        text-align: center;
        margin-bottom: 1.2rem;
        font-family: $krona-one;
        font-size: 2.5rem; }
    hr {
        margin-top: 0;
        margin-bottom: 3.5rem;
        border-color: $main-color;
        border-width: 0.1rem;
        width: 15%; } }


